import { mapGetters } from 'vuex';
import ArrowLeft from 'vue-material-design-icons/ArrowLeft';
import { NcEmptyContent, NcActions, NcActionButton, NcLoadingIcon, isMobile } from '@nextcloud/vue';
import File from '../components/File.vue';
import FilesListViewer from '../components/FilesListViewer.vue';
import SemaphoreWithPriority from '../utils/semaphoreWithPriority.js';
import FilesSelectionMixin from '../mixins/FilesSelectionMixin.js';
import AbortControllerMixin from '../mixins/AbortControllerMixin.js';
export default {
  name: 'TagContent',
  components: {
    File,
    FilesListViewer,
    NcEmptyContent,
    NcActions,
    NcActionButton,
    NcLoadingIcon,
    ArrowLeft
  },
  mixins: [isMobile, FilesSelectionMixin, AbortControllerMixin],
  props: {
    path: {
      type: String,
      default: ''
    }
  },

  data() {
    return {
      error: null,
      loading: false,
      semaphore: new SemaphoreWithPriority(30),
      appContent: document.getElementById('app-content-vue')
    };
  },

  computed: { // global lists
    ...mapGetters(['files', 'tags', 'tagsNames']),

    // current tag id from current path
    tagId() {
      return this.$store.getters.tagId(this.path);
    },

    // current tag
    tag() {
      return this.tags[this.tagId];
    },

    // files list of the current tag
    fileIds() {
      return this.tag ? this.tag.files : [];
    },

    isEmpty() {
      return this.fileIds.length === 0;
    }

  },
  watch: {
    async path() {
      this.fetchContent();
    }

  },

  async beforeMount() {
    this.fetchContent();
  },

  methods: {
    async fetchContent() {
      // close any potential opened viewer
      OCA.Viewer.close();
      this.loading = true;
      this.error = null;

      try {
        // if we don't already have some cached data let's show a loader
        if (!this.tags[this.tagId]) {
          await this.$store.dispatch('fetchAllTags', {
            signal: this.abortController.signal
          });
        }

        await this.$store.dispatch('fetchTagFiles', {
          id: this.tagId,
          signal: this.abortController.signal
        });
      } catch (error) {
        console.error(error);
        this.error = true;
      } finally {
        // done loading
        this.loading = false;
      }
    },

    openViewer(fileId) {
      const file = this.files[fileId];
      OCA.Viewer.open({
        path: file.filename,
        list: this.fileIds.map(fileId => this.files[fileId]),
        loadMore: file.loadMore ? async () => await file.loadMore(true) : () => [],
        canLoop: file.canLoop
      });
    }

  }
};